import { Card, Descriptions, Divider, Image, Space, Typography, Upload } from "antd"
import { observer } from "mobx-react-lite"
import { userStore } from "../../store/userStore"
import { UploadOutlined } from "@ant-design/icons";




function PaymentInfo(){

    const {user, updateUser} = userStore;
    const {firstname, lastname, paymentQR1, paymentQR2} = user;


    // upload
    const uploadQR1 = {
        name: 'file',
        action: process.env.REACT_APP_BE_IMAGE_UPLOAD_API,
        headers: {
            authorization: 'Basic c29sYXI6NjY2NjY2',
        },
        onChange(info) {
            if (info.file.status !== 'uploading') {
            console.log(info.file, info.fileList);
            }
            if (info.file.status === 'done') {
            // updateImage( info?.file?.response);
            updateUser({paymentQR1: info?.file?.response})
            console.log(`${info.file.name} file uploaded successfully`)
            // message.success(`${info.file.name} file uploaded successfully`);
            } else if (info.file.status === 'error') {
            console.log(`${info.file.name} file upload failed`)
            // message.error(`${info.file.name} file upload failed.`);
            }
        },
        };

    // upload
    const uploadQR2 = {
        name: 'file',
        action: process.env.REACT_APP_BE_IMAGE_UPLOAD_API,
        headers: {
            authorization: 'Basic c29sYXI6NjY2NjY2',
        },
        onChange(info) {
            if (info.file.status !== 'uploading') {
            console.log(info.file, info.fileList);
            }
            if (info.file.status === 'done') {
                updateUser({paymentQR2: info?.file?.response})
            console.log(`${info.file.name} file uploaded successfully`)
            // message.success(`${info.file.name} file uploaded successfully`);
            } else if (info.file.status === 'error') {
            console.log(`${info.file.name} file upload failed`)
            // message.error(`${info.file.name} file upload failed.`);
            }
        },
        };

    //
    const updateBank1 = (value) =>{

        updateUser(value);
    }
    return (
        <>
        <Divider orientation="left">收款信息</Divider>
        {
            
            // basic info
            <Card>

                <Descriptions size="middle" column={1} title='银行 1'>

                
            
                        <Descriptions.Item label='银行'>
                            <Typography.Text editable={{onChange: v => updateBank1({bank1:v})}}>{user.bank1}</Typography.Text> 
                        </Descriptions.Item>
        
                        <Descriptions.Item label='支行'>         
                            <Typography.Text editable={{onChange: v => updateBank1({branch1:v})}}>{user.branch1}</Typography.Text> 
            
                        </Descriptions.Item>
        
                        <Descriptions.Item label='账号'>      
                            <Typography.Text editable={{onChange: v => updateBank1({account1:v})}}>{user.account1}</Typography.Text> 
                        </Descriptions.Item>
        
                        <Descriptions.Item label='户名'>   
                            <Typography.Text editable={{onChange: v => updateBank1({owner1:v})}}>{user.owner1}</Typography.Text> 
                        </Descriptions.Item>
                        </Descriptions>


                <Descriptions size="middle" column={1} title='银行 2'>



                        <Descriptions.Item label='银行'>
                            <Typography.Text editable={{onChange: v => updateBank1({bank2:v})}}>{user.bank2}</Typography.Text> 
                        </Descriptions.Item>
        
                        <Descriptions.Item label='支行'>         
                            <Typography.Text editable={{onChange: v => updateBank1({branch2:v})}}>{user.branch2}</Typography.Text> 
                        </Descriptions.Item>
        
                        <Descriptions.Item label='账号'>      
                            <Typography.Text editable={{onChange: v => updateBank1({account2:v})}}>{user.account2}</Typography.Text> 
                        </Descriptions.Item>
        
                        <Descriptions.Item label='户名'>   
                            <Typography.Text editable={{onChange: v => updateBank1({owner2:v})}}>{user.owner2}</Typography.Text> 
                        </Descriptions.Item>
   
                        </Descriptions>


                <Descriptions size="middle" column={1} title='收款码'>

                    
                    <Descriptions.Item label='收款码'>

                        <Space direction="vertical">
                        <Image
                            src={`${process.env.REACT_APP_BE_IMAGE_API}${paymentQR1}`}
                            fallback="data:image/png;base64,"
                        />
                        <Upload {...uploadQR1} style={{position: ''}}>
                        <   UploadOutlined /> 更新图片
                        </Upload>

                        </Space>
                    </Descriptions.Item>
                    
                    <Descriptions.Item label='收款码'>

                        <Space direction="vertical">
                    
                        <Image
                            src={`${process.env.REACT_APP_BE_IMAGE_API}${paymentQR2}`}
                            fallback="data:image/png;base64,"
                        />
                        
                        <Upload {...uploadQR2} style={{position: ''}}>
                        <   UploadOutlined /> 更新图片
                        </Upload>
                        </Space>
                    </Descriptions.Item>
      
                </Descriptions>
            </Card>
            // QR Code
        }
        </>
    )
}


export default observer(PaymentInfo)